
import contact from "../../vuex/contacts"
import { aliLogin } from "../chat/api/aliLogin"
import axios from 'axios'
import Toast from 'vux/src/components/toast/index.vue'

export default {
data() {
const userName = window.localStorage.getItem('userName')
const friendName = window.localStorage.getItem('friendName')
const userId = window.localStorage.getItem('userId')
const nickName = window.localStorage.getItem('nickName')
const myId = this.$route.params.id
return {
userName,
friendName,
userId,
nickName,
hasFriend: false,
myId,
infos:[],
friendsId:[],
allFriends: [],
friendImg:''
}
},
created () {
axios
.get('http://121.42.177.97:8080/red/user/find_id.do?friend_id=' + this.myId)
.then(res => {

this.infos= res.data.data[0]
console.log(this.infos)
})
.catch(res => {
throw res
})
},
mounted () {
axios
.post('http://121.42.177.97:8080/red/user/allfriend.do?name=' + this.userName)
.then(res => {
for(var val of res.data.data){

this.friendsId.push(val.friend_id)
}
this.myId = parseFloat(this.myId)
if(this.friendsId.indexOf(this.myId) === -1){
this.hasFriend = true

}
else{

var list = res.data.data
var listlen = list.length
for (var i=0;i<listlen;i++){
var item = list[i];
var friend_id = item.friend_id
if(this.myId === friend_id){
this.allFriends = item
console.log(this.allFriends)
window.localStorage.setItem("friendTouid", this.allFriends.im_user)
window.localStorage.setItem("friendNick", this.allFriends.friend_nick)
}
}
}
})
.catch(res => {
throw res
})
},
methods: {
addFriend () {
axios
.post('http://121.42.177.97:8080/red/user/addfriend.do?name=' + this.userName + '&' + 'id=' + this.userId)
.then(res => {
if (res.data.data === 1) {
console.log(res.data)
this.$router.push({ path: "/contact/new-friends" })
} else {
this.$vux.toast.show({
type: 'cancel',
text: '不能重复添加好友'
})
}
})
.catch(res => {
throw res
})
}
}
}



